import React from 'react';
import {
	Row,
	Col,
	Form,
	FormGroup,
	Label,
	Input,
	FormText,
	Button
} from 'reactstrap';
import $ from 'jquery';

import './PostForm.css';

export default class PostForm extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			open: false,
			waiting: false
		};

		this.db = window.firebase.database();
		this.toggleOpen = this.toggleOpen.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	toggleOpen() {
		this.setState((prevState) => ({
			open: !prevState.open
		}));
	}

	async handleSubmit() {
		const name = this.name.value;
		const dept = this.dept.value;
		const desc = this.desc.value;
		const freeTime = this.freeTime.value;
		const steal = $('.PostForm__input-steal').is(':checked');
		const file = $('#PostForm__file')[0].files[0];
		const pass = this.pass.value;
		const color = '#000000';
		if (!name || !dept || !desc || !freeTime || !file || !pass) {
			alert('');
			return;
		}

		const validType = ['png', 'jpg', 'jpeg'];
		const fileType = file.name.split('.').pop().toLowerCase();
		if (!validType.includes(fileType)) {
			alert(' PNG  JPG ');
			return;
		}

		this.setState({
			waiting: true
		});

		// set localStorage to keep name
		localStorage.CYTau = localStorage.CYTau || '{}';
		const data = JSON.parse(localStorage.CYTau);
		data.name = name;
		data.color = color;
		localStorage.CYTau = JSON.stringify(data);

		// upload img to imgut
		const url = await this.uploadImg(file);

		const key = window.sha3_512(pass).substring(0, 10) + Date.now().toString(16).substring(7);
		this.db.ref(`list/${key}`).set(JSON.stringify({
			name,
			dept,
			desc,
			freeTime,
			steal,
			url,
			color
		})).then(() => {
			this.setState({
				waiting: false
			});

			this.props.onPost();
		});
	}

	uploadImg(file) {
		const fd = new FormData();
		fd.append('image', file);
		return new Promise((resolve, reject) => {
			$.ajax({
				url: 'https://api.imgur.com/3/image',
				type: 'post',
				dataType: 'json',
				cressDomain: true,
				processData: false,
				contentType: false,
				mimeType: 'multipart/form-data',
				headers: {
					authorization: 'Client-ID 8182977741f850e'
				},
				data: fd,
				success: function (data) {
					resolve(data.data.link);
				},
				error: function (jqXHR) {
					console.error(jqXHR);
				}
			})
		});
	}

	render() {
		const depts = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
		const quotationList = [
			'',
			'',
			' ',
			'',
			'',
			'',
			'',
			'  ',
			'?\\',
			' ',
			'??',
			'',
			'?',
			'?',
			'',
			'',
			'ig?',
			'?',
			'',
			'?',
			'   ?',
			'??'
		];
		const quot = quotationList[Math.floor(Math.random() * quotationList.length)];
		let name = '';
		if (localStorage.CYTau && JSON.parse(localStorage.CYTau) && JSON.parse(localStorage.CYTau).name) {
			name = JSON.parse(localStorage.CYTau).name;
		}

		return (
			<Row className="pt-4">
				<Col className="PostForm" xs={12}>
					<span className="PostForm__title" onClick={this.toggleOpen}>
						 <i className={`fa fa-chevron-${this.state.open ? 'up' : 'down'}`} aria-hidden="true"></i>
					</span>
					<div className={`PostForm__formWrap ${!this.state.open ? 'close' : ''}`}>
						<Form>
							<FormGroup row>
								<Label for="PostForm__input-name" sm={2}></Label>
								<Col sm={10}>
									<Input type="text" id="PostForm__input-name" placeholder="" defaultValue={name} innerRef={node => this.name = node} />
								</Col>
							</FormGroup>
							<FormGroup row>
								<Label for="PostForm__inpit-dept" sm={2}></Label>
								<Col sm={10}>
									<Input type="select" id="PostForm__inpit-dept" innerRef={node => {this.dept = node}}>
										{
											depts.map((val, i) => (
												<option key={val} value={val}>{val}</option>
											))
										}
									</Input>

								</Col>
							</FormGroup>
							<FormGroup row>
								<Label for="PostForm__input-desc" sm={2}></Label>
								<Col sm={10}>
									<Input type="textarea" id="PostForm__input-desc" defaultValue={quot} innerRef={node => {this.desc = node}} />
								</Col>
							</FormGroup>
							<FormGroup row>
								<Label for="PostForm__input-freeTime" sm={2}></Label>
								<Col sm={10}>
									<Input type="text" id="PostForm__input-freeTime" placeholder="7 6   ?" innerRef={node => {this.freeTime = node}} />
								</Col>
							</FormGroup>
							<FormGroup row>
								<Label sm={2}></Label>
								<Col sm={10}>
									<Label check>
										<Input className="PostForm__input-steal" type="checkbox" />{' '}
									</Label>
								</Col>
							</FormGroup>
							<FormGroup row>
								<Label for="PostForm__file" sm={2}></Label>
								<Col sm={10}>
									<Input type="file" id="PostForm__file" />
									<FormText color="muted">
										.jpg/.png
									</FormText>
								</Col>
							</FormGroup>
							<FormGroup row>
								<Label for="PostForm__input-pass" sm={2}></Label>
								<Col sm={10}>
									<Input type="text" id="PostForm__input-pass" innerRef={node => {this.pass = node}} />
								</Col>
							</FormGroup>
							<FormGroup row>
								<Col sm={12}>
									<Button
										disabled={this.state.waiting ? true : false}
										className="PostForm__btn-submit" block color="primary"
										onClick={this.handleSubmit}>
											{ this.state.waiting ? '...' : ''}
									</Button>
								</Col>
							</FormGroup>
						</Form>
					</div>
				</Col>
			</Row>
		)
	}
}



